.my-work__tile {
  width: 350px;
  height: 350px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px 15px;
  margin-top: 0px;
  box-shadow: -14px 0px 47px -1px rgba(0,0,0,0);
  transition: box-shadow .4s ease, transform .4s ease;
  transform: scale(1);
}

.my-work__tile:hover {
  box-shadow: -14px 0px 47px -1px rgba(0,0,0,0.4);
  transform: scale(1.05);
}

.my-work__overlay {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .58);
}

.my-work__label {
  color: white;
  position: relative;
  z-index: 2;
  padding-bottom: 4px;
  border-bottom: 1px solid white;
}
